.auth {
  width: 100%;
  max-width: 935px;
  margin: 0 auto;
  display: flex;
  min-height: 100%;
  margin-top: 30px;
  justify-content: center;
  align-items: center; }
  .auth .column {
    flex: 1; }
    .auth .column:last-child {
      width: 38%;
      margin-left: 35px; }
    .auth .column .appBox {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 35px; }
      .auth .column .appBox .appstore {
        margin-top: 25px; }
        .auth .column .appBox .appstore img {
          height: 40px; }
          .auth .column .appBox .appstore img:first-child {
            margin-right: 10px; }
    .auth .column .whiteBox {
      text-align: center;
      padding: 15px;
      border: 1px solid #e3e3e3;
      background-color: white; }
      .auth .column .whiteBox .logo img {
        height: 300px;
        width: auto; }
      .auth .column .whiteBox .change-link {
        color: #73bb2b;
        cursor: pointer; }
      .auth .column .whiteBox p {
        margin: 15px; }
    .auth .column .form-box {
      margin-bottom: 10px;
      padding: 0 40px;
      padding-top: 30px; }
      .auth .column .form-box img {
        height: 60px; }

.form-component {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px; }
  .form-component .form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .form-component .form input[type="submit"] {
      margin-top: 10px; }
    .form-component .form .text-input {
      border: 1px solid #efefef;
      border-radius: 3px;
      background-color: #fafafa;
      margin-bottom: 5px;
      font-size: 15px;
      padding: 9px 0 7px 8px;
      font-weight: 400; }
      .form-component .form .text-input:focus {
        outline: none;
        border: 1px solid #b2b2b2; }
  .form-component .button {
    -webkit-appearance: none;
    border-radius: 3px;
    background-color: #73bb2b;
    color: white;
    font-weight: 600;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    line-height: 26px;
    width: 22%;
    cursor: pointer; }
    .form-component .button:focus, .form-component .button:active {
      outline: none; }
    .form-component .button:active {
      opacity: 0.9; }
    .form-component .button svg {
      margin-right: 10px; }
  .form-component .divider {
    text-transform: uppercase;
    color: #737373;
    font-weight: 600;
    margin: 20px 0;
    margin-bottom: 20px;
    position: relative; }
    .form-component .divider:before {
      content: "";
      width: 42%;
      position: absolute;
      background-color: #737373;
      height: 1px;
      left: 0;
      top: 6px;
      opacity: 0.8; }
    .form-component .divider:after {
      content: "";
      width: 42%;
      position: absolute;
      background-color: #737373;
      height: 1px;
      right: 0;
      top: 6px;
      opacity: 0.8; }
  .form-component .infoContent {
    font-size: 15px;
    color: #737373; }
    .form-component .infoContent .menuHeader {
      cursor: pointer;
      display: flex;
      margin-top: 15px;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      text-transform: uppercase; }
    .form-component .infoContent .manualBox {
      display: flex;
      justify-content: center;
      align-items: center; }
      .form-component .infoContent .manualBox .manual {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        margin-top: 10px;
        background-color: #e3e3e3;
        padding: 10px;
        border-radius: 5px;
        font-size: 15px; }
        .form-component .infoContent .manualBox .manual .item:nth-of-type(2) {
          margin-top: 5px;
          margin-bottom: 5px; }
        .form-component .infoContent .manualBox .manual .item:nth-of-type(4) {
          margin-top: 5px;
          margin-bottom: 5px; }
        .form-component .infoContent .manualBox .manual .item:nth-of-type(6) {
          margin-top: 5px; }
